.title {
  position: relative;
  z-index: 50;
}
.title h1 {
  font-size: 15rem;
  font-weight: bold;
}
.title h1 span {
  vertical-align: middle;
  margin: 0 30px;
}

.message {
  margin-top: 50px;
}
.message h2 {
  font-size: 1.8rem;
}

.action {
  margin-top: 30px;
}
.action button {
  padding: 10px 20px;
  font-family: Poppins;
  font-size: 1.2rem;
  color: #fff;
  border: 2px solid #ff9c61;
  background-color: #ff9c61;
  border-radius: 50px;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  box-shadow: 0px 0px 15px #ff9c61;
}
.action button:hover {
  cursor: pointer;
  background-color: transparent;
}

.square {
  width: 150px;
  height: 150px;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  left: -30px;
  -moz-transform: scale(1.1) rotate(45deg);
  -ms-transform: scale(1.1) rotate(45deg);
  -webkit-transform: scale(1.1) rotate(45deg);
  transform: scale(1.1) rotate(45deg);
}
.square > * {
  position: absolute;
}
.square .light {
  box-shadow: 0px 0px 10px #fedbae;
  background-color: #fedbae;
}
.square .light1, .square .light3 {
  width: 15px;
  height: 135px;
  -moz-transform: skew(0deg, 45deg);
  -ms-transform: skew(0deg, 45deg);
  -webkit-transform: skew(0deg, 45deg);
  transform: skew(0deg, 45deg);
}
.square .light2, .square .light4 {
  width: 120px;
  height: 15px;
  -moz-transform: skew(45deg);
  -ms-transform: skew(45deg);
  -webkit-transform: skew(45deg);
  transform: skew(45deg);
}
.square .light1 {
  z-index: 10;
  top: 5.5px;
  left: 15px;
}
.square .light3 {
  z-index: 2;
  top: 7.5px;
  right: 15px;
}
.square .light2 {
  top: 0;
  left: 22.5px;
}
.square .light4 {
  bottom: 0;
  right: 22.5px;
}
.square .shadow {
  background-color: #ff9c61;
  box-shadow: 0px 0px 10px #ff9c61;
}
.square .shadow1 {
  bottom: 7.5px;
  right: 0;
  width: 15px;
  height: 120px;
  -moz-transform: skew(0deg, -45deg);
  -ms-transform: skew(0deg, -45deg);
  -webkit-transform: skew(0deg, -45deg);
  transform: skew(0deg, -45deg);
}
.square .shadow2 {
  z-index: 3;
  top: 15px;
  left: 22.5px;
  width: 120px;
  height: 15px;
  -moz-transform: skew(-45deg);
  -ms-transform: skew(-45deg);
  -webkit-transform: skew(-45deg);
  transform: skew(-45deg);
}
.square .stairs1 li, .square .stairs2 li, .square .stairs3 li, .square .stairs4 li {
  position: relative;
  width: 7.5px;
  height: 7.5px;
  background-color: #fedbae;
  box-shadow: 0px 0px 10px #fedbae;
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
